import React from "react";
import PropTypes from "prop-types";
import "./ShareButtons.css";
import styled from 'styled-components';

import {
  FacebookShareButton,
  TwitterShareButton,
  PinterestShareButton,
  RedditShareButton,
  TumblrShareButton,
  EmailShareButton,
} from "react-share";

import { FacebookIcon, TwitterIcon, PinterestIcon, RedditIcon, TumblrIcon, EmailIcon } from "react-share";

const Container = styled.div`
  padding-top: 10px;
  padding-bottom: 5px;
  display: flex;
  /* flex-direction: row; */
  /* flex-wrap: nowrap; */
  /* flex-flow: row; */
  justify-content: center;
`;

const Network = styled.div`
  transition: all 500ms ease;
  vertical-align: top;
  display: inline-block;
  margin-left: 30px;
  margin-right: 30px;
  text-align: center;

  @media screen and (max-width: 700px) {
    margin-left: 15px;
    margin-right: 15px;
  }
`;

class ShareButtons extends React.Component {
  render() {
    let fullTitle = this.props.title + " - Memoirs of an Albatross";
    let shareUrl = this.props.url;
    return (
      <Container>
        <Network>
          <FacebookShareButton url={shareUrl} quote={fullTitle} className="shareButton">
            <FacebookIcon size={32} round />
          </FacebookShareButton>
        </Network>
        <Network>
          <TwitterShareButton url={shareUrl} title={fullTitle} className="shareButton">
            <TwitterIcon size={32} round />
          </TwitterShareButton>
        </Network>
        <Network>
          <PinterestShareButton url={shareUrl} className="shareButton">
            <PinterestIcon size={32} round />
          </PinterestShareButton>
        </Network>
        <Network>
          <RedditShareButton url={shareUrl} title={fullTitle} className="shareButton">
            <RedditIcon size={32} round />
          </RedditShareButton>
        </Network>
        <Network>
          <TumblrShareButton url={shareUrl} title={fullTitle} className="shareButton">
            <TumblrIcon size={32} round />
          </TumblrShareButton>
        </Network>
        <Network>
          <EmailShareButton url={shareUrl} subject={fullTitle} className="shareButton">
            <EmailIcon size={32} round />
          </EmailShareButton>
        </Network>
      </Container>
    );
  }
}

ShareButtons.propTypes = {
  url: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};

export default ShareButtons;
