import React from "react";
import "./ContactForm.css";

// Reference for autoresizing textarea: https://jsfiddle.net/kx4vt4uj/1/ by https://www.reddit.com/user/wasd171
const albymail = "albatross.memoirs@protonmail.com";
const lineHeight = 18;

class ContactForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      subject: "",
      message: "",
      rows: 1,
    };

    this.handleChangeSubject = this.handleChangeSubject.bind(this);
    this.handleChangeMessage = this.handleChangeMessage.bind(this);
  }

  handleChangeSubject(event) {
    this.setState({
      subject: event.target.value,
    });
  }

  handleChangeMessage(event) {
    // To cause proper recalc when deleting lines
    const oldRows = event.target.rows;
    event.target.rows = 1;
    const newRows = ~~(event.target.scrollHeight / lineHeight);

    if (newRows === oldRows) {
      event.target.rows = newRows;
    }

    this.setState({
      message: event.target.value,
      rows: newRows,
    });
  }

  render() {
    return (
      <form className="ContactForm" onSubmit={this.handleSubmit}>
        <div>
          <label>
            <input type="text" placeholder="Subject" value={this.state.subject} onChange={this.handleChangeSubject} />
          </label>
        </div>
        <div>
          <label>
            <textarea
              className="ContactFormMessage"
              rows={this.state.rows}
              placeholder="Message"
              value={this.state.message}
              onChange={this.handleChangeMessage}
              style={{ lineHeight: `${lineHeight}px` }}
            />
          </label>
        </div>
        <a
          className="ContactFormButton"
          href={`mailto:${albymail}?subject=${this.state.subject}&body=${this.state.message.replace(/\n/g, "%0D%0A")}`}
        >
          Send
        </a>
      </form>
    );
  }
}

export default ContactForm;
