import React from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";

/**
 * Given a DOM element, searches it for <img> tags and checks if all of them
 * have finished loading or not.
 * @param  {Element} parentNode
 * @return {Boolean}
 */
function imagesLoaded(parentNode) {
  const imgElements = parentNode.querySelectorAll("img");
  for (const img of imgElements) {
    if (!img.complete) {
      return false;
    }
  }
  return true;
}

class ComicGallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }

  handleStateChange = () => {
    this.setState({
      loading: !imagesLoaded(this.galleryElement),
    });
  };

  renderSpinner() {
    if (!this.state.loading) {
      // Render nothing if not loading
      return null;
    }
    return <span className="spinner" />;
  }

  renderImage(imageUrl, imageLink, imageTitle, imageDate) {
    return (
      <span tabIndex="0">
        <Link to={imageLink}>
          {/* <img src={imageUrl}
                className="Archive-thumb"
                onLoad={this.handleStateChange}
                onError={this.handleStateChange}
                /> */}

          <h4>
            {" "}
            {imageDate.month}/{imageDate.day}/{imageDate.year} - {imageTitle}
          </h4>
        </Link>
      </span>
    );
  }

  render() {
    return (
      <div
        className="gallery"
        ref={element => {
          this.galleryElement = element;
        }}
      >
        {this.renderSpinner()}
        <div className="images">
          {this.props.imageThumbs.map(thumb => this.renderImage(thumb.url, thumb.link, thumb.title, thumb.date))}
        </div>
      </div>
    );
  }
}

ComicGallery.propTypes = {
  imageThumbs: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default ComicGallery;
