import ComicData from "./comics.json";
import ComicSources from "./comics/index";

const dateObjToDate = date => Date.UTC(+date.year, date.month - 1, +date.day, 0, 0);

export const getComicData = id => ComicData.find(comic => comic.id === id);
export const getLinkToComic = id => getComicData(id).map(x => `/comic/${x.navigationID}`);
export const getReleasedComics = () =>
  ComicData.sort(sortByComicDate).filter(x => dateObjToDate(x.date) < new Date().getTime());
export const getReleasedComicsOrder = () => getReleasedComics().map(x => x.navigatorID);
export const getComicSource = id => {
  return ComicSources[id];
};

const sortByComicDate = (a, b) => {
  return dateObjToDate(a.date) - dateObjToDate(b.date);
};

export const getArchiveEntries = () =>
  getReleasedComics()
    .map(x => {
      return {
        date: x.date,
        link: `/comic/${x.navigatorID}`,
        title: x.title,
      };
    })
    .reverse();
