import React from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { getReleasedComicsOrder } from "../../assets/LocalDB";
import styled from "styled-components";

const comicOrder = getReleasedComicsOrder();
const linkToOldest = "/comic/" + comicOrder[0];
const linkToNewest = "/comic/" + comicOrder[comicOrder.length - 1];

const NavBarRow = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;

  @media screen and (max-width: 700px) {
    display: flex;
  }
`;

const NavBarItem = styled.li`
  float: left;
  box-sizing: border-box;
  display: block;
  color: #aaa;
  text-align: center;
  padding: 8px 16px;
  margin: 10px 10px;
  text-decoration: none;

  @media screen and (max-width: 700px) {
    float: left;
    padding: 3px 6px;
    margin: 2px 2px;
    border-radius: 2px;
  }
`;

const NavBarItemLink = styled.li`
  float: left;

  & a {
    box-sizing: border-box;
    transition: all 300ms ease;
    display: block;
    color: #222;
    text-align: center;
    padding: 8px 16px;
    margin: 10px 10px;
    text-decoration: none;
    border-radius: 10px;
  }

  & a:hover {
    background-color: #444;
    color: white;
    text-decoration: none;
    border-radius: 10px;
  }

  @media screen and (max-width: 700px) {
    float: left;

    & a {
      padding: 3px 6px;
      margin: 2px 2px;
      border-radius: 2px;
    }
  }
`;

function renderPreviousLink(currentIndex) {
  if (currentIndex !== 0) {
    const linkToPrevious = "/comic/" + comicOrder[currentIndex - 1];
    return (
      <NavBarItemLink>
        <Link to={linkToPrevious}>Previous</Link>
      </NavBarItemLink>
    );
  } else {
    return <NavBarItem>Previous</NavBarItem>;
  }
}

function renderNextLink(currentIndex) {
  if (currentIndex !== comicOrder.length - 1) {
    const linkToNext = "/comic/" + comicOrder[currentIndex + 1];
    return (
      <NavBarItemLink>
        <Link to={linkToNext}>Next</Link>
      </NavBarItemLink>
    );
  } else {
    return <NavBarItem>Next</NavBarItem>;
  }
}

function renderRandomLink(currentIndex) {
  const orderExceptCurrent = comicOrder.slice();
  orderExceptCurrent.splice(currentIndex, 1);
  if (orderExceptCurrent.length > 0) {
    const linkToRandom = "/comic/" + orderExceptCurrent[Math.floor(Math.random() * orderExceptCurrent.length)];
    return (
      <NavBarItemLink>
        <Link to={linkToRandom}>Random</Link>
      </NavBarItemLink>
    );
  } else {
    return <NavBarItem>Random</NavBarItem>;
  }
}

const ComicNavigator = props => {
  const { current } = props;
  const currentIndex = comicOrder.indexOf(current);

  return (
    <NavBarRow>
      <NavBarItemLink>
        <Link to={linkToOldest}>Oldest</Link>
      </NavBarItemLink>
      {renderPreviousLink(currentIndex)}
      {renderRandomLink(currentIndex)}
      {renderNextLink(currentIndex)}
      <NavBarItemLink>
        <Link to={linkToNewest}>Latest</Link>
      </NavBarItemLink>
    </NavBarRow>
  );
};

ComicNavigator.propTypes = {
  current: PropTypes.string.isRequired,
};

export default ComicNavigator;
