import React from "react";
import styled from "styled-components";

const contribution = false;

const Container = styled.div`
  padding: 15px;
`;

const Attribution = styled.div`
  text-align: center;
  font-size: 0.5em;

  &a {
    color: rgb(27, 27, 46);
    text-decoration: none;
    font-weight: bold;
  }
`;

const NoReferrerAnchor = (url, text) => (
  <a target="_blank" rel="noopener noreferrer" href={url}>
    {text}
  </a>
);

const Footer = () => (
  <Container>
    {contribution && (
      <Attribution>
        Background image <NoReferrerAnchor url="https://creativecommons.org/licenses/by/2.0/" text="CC by 2.0" />
        Kevin Dooley,{" "}
        <NoReferrerAnchor
          url="https://www.flickr.com/photos/pagedooley/3099699665/"
          text={'"Dicken\'s Village - Teaman & Crupp Fine English China"'}
        />
      </Attribution>
    )}
  </Container>
);

export default Footer;
