import React from "react";
import newLogo from "../../packedResources/header/AlbatrossLogo_wide_text2_150.png";
import newLogoSm from "../../packedResources/header/AlbatrossLogo_mid_text_150.png";
import { Link } from "react-router-dom";
import SocialHomes from "./SocialHomes";
import styled from "styled-components";

const HeaderBlock = styled.div``;

const HeaderTop = styled.header`
  max-width: 100%;
  color: #222;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const HeaderBottom = styled.header`
  color: #222;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
`;

const Row = styled.div``;

const NavBarItem = styled.li`
  float: left;

  & a {
    box-sizing: border-box;
    transition: all 300ms ease;
    display: block;
    color: #222;
    text-align: center;
    padding: 8px 16px;
    margin: 10px 10px;
    text-decoration: none;
    border-radius: 10px;
  }

  & a:hover {
    background-color: #444;
    color: white;
    text-decoration: none;
    border-radius: 10px;
  }

  @media screen and (max-width: 700px) {
    float: left;

    & a {
      padding: 3px 6px;
      margin: 2px 2px;
      border-radius: 2px;
    }
  }
`;

const LogoImage = styled.img`
  display: block;
  max-height: 150px;
  width: auto;
  height: auto;

  @media screen and (max-width: 700px) {
    display: none;
  }
`;

const LogoImageSmall = styled.img`
  display: none;
  max-height: 150px;
  width: auto;
  height: auto;

  @media screen and (max-width: 700px) {
    display: block;
  }
`;

const NavBarRow = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;

  @media screen and (max-width: 700px) {
    display: flex;
  }
`;

const HRTopShadow = styled.hr`
height: 12px;
border: 0;
box-shadow: inset 0px -2px 2px -2px rgba(0, 0, 0, 0.5);
`;

const HRBottomShadow = styled.hr`
height: 12px;
border: 0;
box-shadow: inset 0 12px 12px -12px rgba(0, 0, 0, 0.5);
`;

const navLinks = [
  { id: "Home", path: "/" },
  { id: "Archive", path: "/archive" },
  { id: "Contact", path: "/contact" },
  { id: "About", path: "/about" },
];

const Header = () => (
  <HeaderBlock>
    <div className="row">
      <div className="col-sm-2" />
      <div className="col-sm-8">
        <HeaderTop>
          <Link to="/">
            <LogoImage src={newLogo} alt="logo" />
            <LogoImageSmall src={newLogoSm} alt="logo" />
          </Link>
        </HeaderTop>
        <HRTopShadow />
        <HeaderBottom>
          <nav className="navbar navbar-expand-sm justify-content-center">
            <NavBarRow>
              {navLinks.map(item => {
                return (
                  <NavBarItem>
                    <Link className="nav-link" to={item.path}>
                      {item.id}
                    </Link>
                  </NavBarItem>
                );
              })}
            </NavBarRow>
          </nav>
          <SocialHomes />
        </HeaderBottom>
        <HRBottomShadow />
      </div>
      <div className="col-sm-2" />
    </div>
  </HeaderBlock>
);

export default Header;
