import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";
import AboutPage from "../about/AboutPage";
import ArchivePage from "../archive/ArchivePage";
import ContactPage from "../contact/ContactPage";
import GoogleProof from "../utilities/GoogleProof";
import ReactGA from "react-ga";
import Comic from "../comic/Comic";
import { getReleasedComics } from "../../assets/LocalDB";

const comicOrder = getReleasedComics();

class Router extends Component {
  componentDidUpdate() {
    ReactGA.pageview(window.location.href);
  }

  render() {
    const latest = comicOrder[comicOrder.length - 1];

    return (
      <Switch>
        {/* Main navigation */}
        <Route exact path="/" render={routeProps => <Comic {...routeProps} id={latest.id} />} />
        <Route path="/archive" component={ArchivePage} />
        <Route path="/contact" component={ContactPage} />
        <Route path="/about" component={AboutPage} />
        <Route path="/google1baee13adfb32074.html" component={GoogleProof} />

        {/* Manga pages */}
        {comicOrder.map(comic => {
          return (
            <Route
              path={`/comic/${comic.navigatorID}`}
              render={routeProps => <Comic {...routeProps} id={comic.id} />}
            />
          );
        })}
        {/* <Route component={NoMangaMatch} /> */}
      </Switch>
    );
  }
}

export default Router;
