import React from "react";
import { getComicData, getComicSource } from "../../assets/LocalDB";
import { prepAltText } from "../utilities/MangaUtils";
import ComicNavigator from "./ComicNavigator";
import ShareButtons from "../utilities/ShareButtons";
import Proverb from "../proverb/Proverb";
import styled from "styled-components";
import PageTitle from '../PageTitle';

const Container = styled.div`
  max-width: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
`;

const Image = styled.img`
  max-width: 100%;
  min-height: 207px;
  align-items: center;
`;

const Comic = props => {
  let mangaData = getComicData(props.id);
  mangaData.altText = prepAltText(mangaData.panelText);
  mangaData.page = getComicSource(props.id);
  return (
    <Container>
      <PageTitle title={mangaData.title} />
      <ComicNavigator current={mangaData.navigatorID} />
      <span tabIndex="0">
        <Image src={mangaData.page} alt={mangaData.altText} />
      </span>
      <Proverb text={mangaData.proverb} author={mangaData.proverbAuthor} />
      <ShareButtons url={"http://www.albatrossmemoirs.com/comic/" + mangaData.navigatorID} title={mangaData.title} />
      <ComicNavigator current={mangaData.navigatorID} />
    </Container>
  );
};

export default Comic;
